import React, {Component, Fragment} from 'react';
import {Table} from 'antd';
import {Link, NavLink} from "react-router-dom";
import axios from "axios";
import 'antd/dist/antd.css';  // or 'antd/dist/antd.less'
import moment from 'moment';
import { DatePicker } from 'antd';
const { RangePicker } = DatePicker;
const dateFormat = 'DD.MM.YYYY';

const columns = [{
    title: 'Id',
    dataIndex: 'fa_id',
    width: '20%',
    render: (id) =>
        <div>
            <Link to={`/feedback/detail/${id}`}>{id}</Link>
        </div>
}, {
    title: 'Customer',
    dataIndex: 'determined_customer',
    width: '20%',
}, {
    title: 'upload_start',
    dataIndex: 'upload_start',
    width: '40%',
    render: (datetime) =>
        <div>{moment(datetime).format('L') + " " + moment(datetime).format('LTS')}</div>
},

];

class FeedbackList extends Component {
    constructor(props) {
        super(props);

        this.state = {
            feedback: [],
            pagination: {},
            loading: false,
            redirect: false,
            redirectTo: null,
        };
    }

    componentDidMount(){
        const now = moment(new Date()).add(1,"day").format( 'DD.MM.YYYY');
        this.fetch(now, now);
    }

    onChange(date, dateString){
        this.fetch(dateString[0], dateString[1]);
    }

    fetch = (from, to) => {
        this.setState({ loading: true });
        axios.post(`/api/feedback`, {from: from, to: to})
            .then((response) => {
                const data = response.data;
                const pagination = { ...this.state.pagination };
                // Read total count from server
                 pagination.total = data.data.length;
                //pagination.total = 200;
                this.setState({
                    loading: false,
                    feedback: data.data,
                    pagination,
                });
        })
        .catch(error => console.log(error));
    };

    render() {
        return (
            <React.Fragment>
                <RangePicker defaultValue={[moment(new Date(), dateFormat), moment(new Date(), dateFormat)]}
                             format={dateFormat} onChange={this.onChange.bind(this)}
                />

            <Table
                columns={columns}
                rowKey={record => record.fa_id}
                dataSource={this.state.feedback}
                pagination={this.state.pagination}
                loading={this.state.loading}
                size="small"
            />
            </React.Fragment>)
    }
}

export default FeedbackList;