import React, {Component, Fragment} from 'react';
import axios from "axios";
import {Radio} from "antd";

import HighchartsReact from 'highcharts-react-official';
import moment from 'moment';
import {Highcharts as HighStock} from 'highcharts/highstock';
import {Highcharts as HighMap} from 'highcharts/highmaps';



class Dashboard extends Component {
    state = {
        loading: false,
        categories: [],
        data: [],
        period: 'week',
        title: 'Week summary'
    };

    componentDidMount(){
        this.createCategoriesAndData(this.state.period);
        this.fetch(this.state.period);
    };

    handlePeriodChange = (e) => {
        let period = e.target.value;
        this.createCategoriesAndData(period);
        this.fetch(period);
    };

    createCategoriesAndData(period){
        let categories = [];
        let title = '';
        let data = [];
        switch (period){
            case 'week':
                for(let i=0; i<=6; i++){
                    const day = moment(new Date()).subtract(i,"day").format( 'DD.MM.YYYY');
                    categories.push(day);
                    data.push(i);
                }
                title = 'Week summary';
                break;
            case 'month':
                for(let i=0; i<=30; i++){
                    const day = moment(new Date()).subtract(i,"day").format( 'DD.MM.YYYY');
                    categories.push(day);
                    data.push(i);
                }
                title = 'Month summary';
                break;
            case 'year':
                for(let i=0; i<=6; i++){
                    const day = moment(new Date()).subtract(i,"day").format( 'DD.MM.YYYY');
                    categories.push(day);
                }
                title = 'Year summary';
                break;
        }
        categories.reverse();
        this.setState({title: title, categories: categories, period: period, data: data});
    }

    fetch = (period) => {
        this.setState({ loading: true });
        axios.get(`/api/feedback/${period}`)
            .then((response) => {
                //const data = response.data;
            })
            .catch(error => console.log(error));
    };

    render() {

        const options = {
            chart: {
                type: 'column'
            },
            title: {
                text: this.state.title
            },
            subtitle: {
                text: 'Subtitle'
            },
            xAxis: {
                categories: this.state.categories,
                crosshair: true
            },
            yAxis: {
                min: 0,
                title: {
                    text: 'Amount'
                }
            },
            tooltip: {
                headerFormat: '<span style="font-size:10px">{point.key}</span><table>',
                pointFormat: '<tr><td style="color:{series.color};padding:0">{series.name}: </td>' +
                    '<td style="padding:0"><b>{point.y:.1f} mm</b></td></tr>',
                footerFormat: '</table>',
                shared: true,
                useHTML: true
            },
            plotOptions: {
                column: {
                    pointPadding: 0.2,
                    borderWidth: 0,

                }
            },
            series: [{
                name: 'Tokyo',
                data: this.state.data

            },
            ]
        };


        const opt = {
            chart: {
                type: 'heatmap',
                inverted: true
            },

            data:
                this.state.data//csv: document.getElementById('csv').innerHTML
            ,

            title: {
                text: 'Highcharts heat map',
                align: 'left'
            },

            subtitle: {
                text: 'Temperature variation by day and hour through May 2017',
                align: 'left'
            },

            xAxis: {
                tickPixelInterval: 50,
                min: Date.UTC(2017, 4, 1),
                max: Date.UTC(2017, 4, 30)
            },

            yAxis: {
                title: {
                    text: null
                },
                labels: {
                    format: '{value}:00'
                },
                minPadding: 0,
                maxPadding: 0,
                startOnTick: false,
                endOnTick: false,
                tickPositions: [0, 6, 12, 18, 24],
                tickWidth: 1,
                min: 0,
                max: 23
            },

            colorAxis: {
                stops: [
                    [0, '#3060cf'],
                    [0.5, '#fffbbc'],
                    [0.9, '#c4463a']
                ],
                min: -5
            },

            series: [{
                borderWidth: 0,
                colsize: 24 * 36e5, // one day
                tooltip: {
                    headerFormat: 'Temperature<br/>',
                    pointFormat: '{point.x:%e %b, %Y} {point.y}:00: <b>{point.value} ℃</b>'
                }
            }]
        };

        return (
            <Fragment>
                <Radio.Group value={this.state.period} onChange={this.handlePeriodChange}>
                    <Radio.Button value="week">Week</Radio.Button>
                    <Radio.Button value="month">Month</Radio.Button>
                    <Radio.Button value="year">Year</Radio.Button>
                </Radio.Group>
                <HighchartsReact highcharts={HighStock} options={options}/>
                {/*<HighchartsReact highcharts={HighMap} options={opt} constructorType={'mapChart'}/>*/}

            </Fragment>
        )
    }
}

export default Dashboard;